.landing-container {
    display: flex;
    min-height: 90vh;
    justify-content: space-between;
    align-items: center;
    background-color: lightslategray;
    padding: 0 32px ;
    font-family: "Bangers", cursive;
    margin: 0;;
    flex-wrap: wrap;
}

.landing-header{
    font-size: 60px;
    letter-spacing: 4px;
    margin: 0;
}

.typewriter-container {
     font-size: 40px;
     letter-spacing: 4px;
}
.hero-img{
    width: 40vw;
    
}


@media (max-width: 800px) {
    .landing-container{
        flex-direction: column;
        align-items: center;
        
    }

    .hero-img{
        width: 80vw;

    }
    .landing-header{
        margin-top: 32px;
        text-align: center;

    }
    .type-writer-container {
        text-align: center;
    }
}
